import React, { useState, useContext, useEffect, useRef, useMemo } from "react";
import "./Task4.css";
import { Context } from "../../Context";
import { HeadingText } from "../HeadingText/HeadingText";
import { polygons } from "./img";

const { polygon1, polygon2, polygon3, polygon4, polygon5, polygon6, polygon7 } =
  polygons;

// Контент модального окна - Алфавит
const AlphabetModalContent = () => {
  return (
    <>
      <h3> Словать</h3>
      <br />
      <p>Много разных слов</p>
    </>
  );
};

// Контент модального окна - Помощь
const HelpModalContent = () => {
  return (
    <>
      <h3>Электронный помощник</h3>
      <br />
      <p>Контент направленный на помощь в обучении</p>
    </>
  );
};

// Изменение заголовка (поддерживаеи html тэги)
// START
// Верхний заголовок - название урока ( <> текст размещать внутри тэгов </> )
const LessonHeading = () => (
  <>
    <p>Аппликация</p>
  </>
);
// Заголовок с заданием к уроку ( <> текст размещать внутри тэгов </> )
const ThemeHeading = () => (
  <>
    <p style={{ textAlign: "center" }}>Собери из деталей многоугольник</p>
  </>
);
// Текс title с переводом (НЕ поддерживает html тэги)
const titleHeading = "First slide";

//варианты ответов
const polygonsImgs = [
  {
    id: 1,
    name: "polygon1",
    src: polygon1,
    tag: "1",
  },
  {
    id: 2,
    name: "polygon2",
    src: polygon2,
    tag: "2",
  },
  {
    id: 3,
    name: "polygon3",
    src: polygon3,
    tag: "3",
  },
  {
    id: 4,
    name: "polygon4",
    src: polygon4,
    tag: "4",
  },
  {
    id: 5,
    name: "polygon5",
    src: polygon5,
    tag: "5",
  },
  {
    id: 6,
    name: "polygon6",
    src: polygon6,
    tag: "6",
  },
  {
    id: 7,
    name: "polygon7",
    src: polygon7,
    tag: "7",
  },
];

function Task4() {
  const {
    helpSetIcon,
    alphabetSetIcon,
    helpSetModalContent,
    alphabetSetModalContent,
    lessonSetStartAction,
    lessonSetResetTask,
    resetTask,
    lessonSetCheckTask,
    checkTask,
    lessonSetAndAnswer,
    lessonSetViewFeedback,
  } = useContext(Context);

  const [sliderShift, setSliderShift] = useState(0)
const slider = useRef(null)
  // Иконки правого поля  - (помощь и алфавит)
  useEffect(() => {
    console.log("mount");
    //  helpSetIcon - иконка электронного помощника (включить - true, отключить - false)
    helpSetIcon(true);
    //  alphabetSetIcon - иконка алфавита (включить - true, отключить - false)
    alphabetSetIcon(true);
    helpSetModalContent(<HelpModalContent />);
    alphabetSetModalContent(<AlphabetModalContent />);
  }, []);

  useEffect(() => {
    if (resetTask) {
    }
  }, [resetTask]);

  useEffect(() => {
    if (checkTask === true) {
    }
    lessonSetCheckTask(false);
  }, [checkTask]);

  function slideBack(){
    setSliderShift(prev=> prev-100)
  }
  function slideForvard(){
    setSliderShift(prev=> prev+100)
  }

  //  Внесение изменений в контент задания
  return (
    <div className="lessonContent">
      <HeadingText
        lessonHeading={<LessonHeading />}
        themeHeading={<ThemeHeading />}
        titleHeading={titleHeading}
        // soundSource={soundSource}
        soundVisible={true}
        titleVisible={true}
      />
      <div className="slideWrapper">
        <div className="drop_zpne"></div>
        <div className="slider_wrapper">
          <div className="back" onClick={slideBack}></div>
          <div className="slider">
            <div className="slider_inner" style={{left: `${sliderShift}px`}} ref={slider}>
              {polygonsImgs.map((item) => (
                <img key={item.id} src={item.src} alt="polygon" />
              ))}
            </div>
          </div>
          <div className="forvard" onClick={slideForvard}></div>
        </div>
      </div>
    </div>
  );
}

export default Task4;
