import React, { useState, useContext, useEffect, useRef, useMemo } from "react";
import "./Task5.css";
import { Context } from "../../Context";
import { HeadingText } from "../HeadingText/HeadingText";
import { polygons } from "./img";
import { pictures } from "./img";

const { square1, square2, square3, square4, tringle } = polygons;

const {
  bag,
  busket,
  car,
  cookie,
  green,
  sign,
  clock,
  lamp,
  pilow,
  russia,
  snake,
  snow,
  tringle2,
} = pictures;
// Контент модального окна - Алфавит
const AlphabetModalContent = () => {
  return (
    <>
      <h3> Словать</h3>
      <br />
      <p>Много разных слов</p>
    </>
  );
};

// Контент модального окна - Помощь
const HelpModalContent = () => {
  return (
    <>
      <h3>Электронный помощник</h3>
      <br />
      <p>Контент направленный на помощь в обучении</p>
    </>
  );
};

// Изменение заголовка (поддерживаеи html тэги)
// START
// Верхний заголовок - название урока ( <> текст размещать внутри тэгов </> )
const LessonHeading = () => (
  <>
    <p>Сортировка иллюстраций</p>
  </>
);
// Заголовок с заданием к уроку ( <> текст размещать внутри тэгов </> )
const ThemeHeading = () => (
  <>
    <p style={{ textAlign: "center" }}>
      Собери вместе предметы, которые похожи на конкретную геометрическую
      фигуру.
    </p>
  </>
);
// Текс title с переводом (НЕ поддерживает html тэги)
const titleHeading = "First slide";

//варианты ответов
const picturesImg = [
  {
    id: 1,
    name: "bag",
    src: bag,
    tag: "3",
  },
  {
    id: 2,
    name: "busket",
    src: busket,
    tag: "3",
  },
  {
    id: 3,
    name: "car",
    src: car,
    tag: "3",
  },
  {
    id: 4,
    name: "cookie",
    src: cookie,
    tag: "2",
  },
  {
    id: 5,
    name: "green",
    src: green,
    tag: "1",
  },
  {
    id: 6,
    name: "sign",
    src: sign,
    tag: "1",
  },
  {
    id: 7,
    name: "clock",
    src: clock,
    tag: "2",
  },
  {
    id: 8,
    name: "lamp",
    src: lamp,
    tag: "3",
  },
  {
    id: 9,
    name: "pilow",
    src: pilow,
    tag: "2",
  },
  {
    id: 10,
    name: "russia",
    src: russia,
    tag: "4",
  },
  {
    id: 11,
    name: "snake",
    src: snake,
    tag: "1",
  },
  {
    id: 12,
    name: "snow",
    src: snow,
    tag: "4",
  },
  {
    id: 13,
    name: "tringle2",
    src: tringle2,
    tag: "1",
  },
];

function Task5() {
  const {
    helpSetIcon,
    alphabetSetIcon,
    helpSetModalContent,
    alphabetSetModalContent,
    lessonSetStartAction,
    lessonSetResetTask,
    resetTask,
    lessonSetCheckTask,
    checkTask,
    lessonSetAndAnswer,
    lessonSetViewFeedback,
  } = useContext(Context);

  const [sliderShift, setSliderShift] = useState(0);
  const [sliderSize, setSliderSize] = useState();
  const [sliderWrapperSize, setSliderWrapperSize] = useState();
  //const [sliderOffset, setSliderOffset] = useState(0)
  const slider = useRef(null);
  const forvordBtn = useRef(null);
  const backBtn = useRef(null);
  const dropPlace = useRef(null);

  // Иконки правого поля  - (помощь и алфавит)
  useEffect(() => {
    console.log("mount");
    //  helpSetIcon - иконка электронного помощника (включить - true, отключить - false)
    helpSetIcon(true);
    //  alphabetSetIcon - иконка алфавита (включить - true, отключить - false)
    alphabetSetIcon(true);
    helpSetModalContent(<HelpModalContent />);
    alphabetSetModalContent(<AlphabetModalContent />);
    setSliderSize(slider.current.scrollWidth);
    setSliderWrapperSize(slider.current.clientWidth);
  }, []);

  useEffect(() => {
    if (sliderShift >= 0) {
      backBtn.current.style.opacity = "0.5";
    } else backBtn.current.style.opacity = "1";
    if (sliderShift <= -sliderSize + sliderWrapperSize) {
      forvordBtn.current.style.opacity = "0.5";
    } else forvordBtn.current.style.opacity = "1";
  }, [sliderShift, sliderSize, sliderWrapperSize]);

  useEffect(() => {
    if (resetTask === true) {
      dropPlace.current.childNodes.forEach((item) => {
        let length = item.childNodes.length
          for (let i = 1; i <length; i++) {
            let pic = item.childNodes[1]
            console.log(item.childNodes.length)
            pic.style.position = "relative";
            pic.style.zIndex = null;
            pic.style.top = null;
            pic.style.left = null;
            slider.current.append(pic);
          }
      });
      //setLines((prev) => prev.sort(() => Math.random() - 0.5))
      lessonSetResetTask(false);
      setSliderSize(slider.current.scrollWidth);
    }
  }, [resetTask]);

  useEffect(() => {
    if (checkTask === true) {
      let winVar = 0;
      dropPlace.current.childNodes.forEach((item) => {
        if (item.childNodes.length > 1) {
          console.log(item.getAttribute("data-tag"))
          for (let i = 1; i < item.childNodes.length; i++) {
            if (
              item.childNodes[i].getAttribute("data-tag") === item.getAttribute("data-tag")
            ) {
              winVar += 1;
            }
          }
        }
      });
      if (winVar === picturesImg.length) {
        lessonSetAndAnswer(true);
      } else lessonSetAndAnswer(false);
    }
    lessonSetCheckTask(false);
  }, [checkTask]);

  function slideBack() {
    if (sliderShift < 0) setSliderShift((prev) => prev + 170);
  }
  function slideForvard() {
    if (sliderShift > -sliderSize + sliderWrapperSize)
      setSliderShift((prev) => prev - 170);
  }

  let draggingItem;
  let elemBelow;
  function mouseDown(event) {
    lessonSetViewFeedback(false);
    draggingItem = event.target;
    draggingItem.style.cursor = "grabbing";
    let shiftX = event.clientX - draggingItem.getBoundingClientRect().left;
    let shiftY = event.clientY - draggingItem.getBoundingClientRect().top;

    draggingItem.style.position = "absolute";
    draggingItem.style.zIndex = 1000;
    document.body.append(draggingItem);

    moveAt(event.pageX, event.pageY);

    function moveAt(pageX, pageY) {
      draggingItem.style.left = pageX - shiftX + "px";
      draggingItem.style.top = pageY - shiftY + "px";
    }

    elemBelow = document.elementFromPoint(event.clientX, event.clientY);

    function onMouseMove(event) {
      moveAt(event.pageX, event.pageY);

      draggingItem.hidden = true;
      elemBelow = document.elementFromPoint(event.clientX, event.clientY);
      draggingItem.hidden = false;

      if (!elemBelow) return;
    }
    document.addEventListener("mousemove", onMouseMove);

    draggingItem.onmouseup = function () {
      document.removeEventListener("mousemove", onMouseMove);
      draggingItem.style.cursor = "grab";

      if (elemBelow.classList.contains("dropPlace_pictures")||elemBelow.parentNode.classList.contains("dropPlace_pictures")) {
        draggingItem.style.bottm = "static";
        draggingItem.style.zIndex = null;
        draggingItem.style.top = null;
        draggingItem.style.left = null;
        elemBelow.append(draggingItem);
        slideBack();
        lessonSetStartAction(true);
      } else {
        draggingItem.style.position = "static";
        draggingItem.style.zIndex = null;
        draggingItem.style.top = null;
        draggingItem.style.left = null;
        slider.current.append(draggingItem);
      }
      setSliderSize(slider.current.scrollWidth);
    };
  }

  //  Внесение изменений в контент задания
  return (
    <div className="lessonContent">
      <HeadingText
        lessonHeading={<LessonHeading />}
        themeHeading={<ThemeHeading />}
        titleHeading={titleHeading}
        // soundSource={soundSource}
        soundVisible={true}
        titleVisible={true}
      />
      <div className="slideWrapper">
        <div className="dropPlaceWrapper_pictures" ref={dropPlace}>
          <div className="dropPlace_pictures" data-tag="1">
            <div className="forms">
              <img src={tringle} alt="tringle"></img>
            </div>
          </div>
          <div className="dropPlace_pictures" data-tag="2">
            <div className="forms">
              <img src={square1} alt="tringle"></img>
              <img src={square2} alt="tringle"></img>
            </div>
          </div>
          <div className="dropPlace_pictures" data-tag="3">
            <div className="forms">
              <img src={square3} alt="tringle"></img>
            </div>
          </div>
          <div className="dropPlace_pictures" data-tag="4">
            <div className="forms">
              <img src={square4} alt="tringle"></img>
            </div>
          </div>
        </div>
        <div className="slider_wrapper">
          <div className="back" onClick={slideBack} ref={backBtn}></div>
          <div className="slider">
            <div
              className="slider_inner"
              style={{ left: `${sliderShift}px` }}
              ref={slider}
            >
              {picturesImg.map((item) => (
                <div
                  className="picture"
                  key={item.id}
                  style={{ backgroundImage: `url(${item.src})` }}
                  onMouseDown={mouseDown}
                  data-tag={item.tag}
                ></div>
              ))}
            </div>
          </div>
          <div
            className="forvard"
            onClick={slideForvard}
            ref={forvordBtn}
          ></div>
        </div>
      </div>
    </div>
  );
}

export default Task5;
